/**
* Styles for both front-end and editor.
* Optimized SCSS structure, preserving all design and functionality.
*/

:root {
	--wp-fundi-grid-rounded-border-radius: 20px;
	--wp-fundi-grid-circle-border-radius: 50%;
}

.wp-fundi-grid-contents {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	justify-items: center;
	width: 100%;
	height: 100%;
	// grid-gap: 30px;
	margin: 0 auto 20px;
	padding-bottom: 50px !important;

	.wp-fundi-grid-content {
		display: flex;
		width: 100%;
		text-align: left;

		.wp-fundi-image {
			padding: 13px;

			@media screen and (max-width: 768px) {
				padding: 9px;
			}
		}

		a {
			display: flex;
			flex-direction: column;
			width: 100%;
			height: auto;
			text-decoration: none;
			border-radius: 2px;
		}

		.wp-fundi-button {
			padding: 0 15px 15px;
			vertical-align: middle;
			line-height: 13px;
			text-transform: capitalize;
			font-weight: 700;
			font-size: 13px;
			transition: color .2s linear;
			color: #222;

			&:hover {
				color: #5bac26;
			}

			i {
				margin: 2px 0 0 5px;
				font-size: 12px;
				vertical-align: middle;
				color: #5bac26;
				display: inline-block;
				width: 20px;
			}
		}

		i.chevron-right:before {
			content: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAyNCAyNCIgYXJpYS1oaWRkZW49InRydWUiIGZvY3VzYWJsZT0iZmFsc2UiPjxwYXRoIGQ9Ik0xMC42IDZMOS40IDdsNC42IDUtNC42IDUgMS4yIDEgNS40LTZ6Ij48L3BhdGg+PC9zdmc+");
		}

		.wp-fundi-title {
			padding: 0 15px 15px;
			flex-grow: 1;

			@media screen and (max-width: 768px) {
				padding-left: 9px;
				padding-right: 9px;
			}

			h2 {
				margin: 0;
				color: #002e5b;
				font-size: 20px;

				&:hover {
					color: #5bac26;
				}
			}

			p {
				color: #777;
				font-size: 13px !important;

				@media screen and (max-width: 768px) {
					font-size: 12px !important;
				}
			}
		}
	}

	.wp-fundi-pagination {
		bottom: 0 !important;

		.swiper-pagination-bullet-active {
			background-color: var(--vistage_blue, #003F5F);
		}
	}
}

// Image style modifiers
@mixin wp-fundi-img-style($radius, $aspect, $height: 100%, $fit: cover) {
	border-radius: $radius;
	aspect-ratio: $aspect;
	display: block;
	width: 100%;
	height: $height;
	object-fit: $fit;
}

.is-style-wp-fundi-rounded .wp-fundi-grid-content .wp-fundi-featured-image .wp-fundi-image img {
	@include wp-fundi-img-style(var(--wp-fundi-grid-circle-border-radius), 1 / 1);
}

.is-style-default .wp-fundi-grid-content .wp-fundi-featured-image .wp-fundi-image img {
	@include wp-fundi-img-style(0%, 1 / 1);
}

.is-style-wp-fundi-rectangle .wp-fundi-grid-content .wp-fundi-featured-image .wp-fundi-image img {
	@include wp-fundi-img-style(0%, 16 / 9, 250px, contain);
}

.is-style-wp-fundi-rounded-rectangle .wp-fundi-grid-content .wp-fundi-featured-image .wp-fundi-image img {
	@include wp-fundi-img-style(var(--wp-fundi-grid-rounded-border-radius), 16 / 9, 250px);
}
